	module.exports = " <div class=detailpage> <div class=\"menu_icon icon-back\" v-go-back></div> <div id=header>权限控制机制和流程审批机制</div> <div class=banner> <img src=" + __webpack_require__(28) + " alt=\"\"> </div> <section> <div class=\"detailinfo lh25\"> <p class=title>权限控制机制和流程审批机制></p> <p class=c3> ◇ 按角色授权的权限控制机制，可以确保工程部经理，经营部经理，会计等不同级别的人员只能看到自己权限内的业务数据，保证了数据的隔离性和安全性。<br/> ◇ 按人员以及负责人级别授权的流程审批机制不但能确保审批流程的严密性，而且适当增加了审批中的灵活性。比如：如果设置流程中某个审批环节的审批人为该部门的一级负责人，那么如果发起该流程的用户就是该部门的一级负责人，那么当流程进行到此处时将会自动跳过该审批环节。 </p> </div> </section> </div> ";

